<template>
    <div class="slider">
        <Col span="8" class="slider-bar">
            <Menu theme="light" :active-name="sliderRoute" @on-select="changeSlider">
                <MenuItem v-for="sliderItem in sliderList" :name="sliderItem.name">
                    <Icon :type="sliderItem.type" />
                    {{sliderItem.title}}
                </MenuItem>
            </Menu>
        </Col>
    </div>
</template>

<script>
export default {
    props: {
        sliderList: Array
    },
    data() {
        return {
            sliderRoute: ''
        }
    },
    mounted() {
        this.sliderRoute = this.$route.name
    },
    methods: {
        changeSlider(name) {
            this.$router.push({name: name})
        }
    }
}
</script>

<style scoped>
    .slider {
        height: 100%;
    }
    .slider-bar {
        position: fixed;
        height: 100%;
        border-right: 1px solid #DBDEE2;
        width: 200px;
    }
    .slider-bar ul {
        margin-left: -40px;
    }
</style>
