import Vue from "vue";
import Router from "vue-router";
import Login from "./views/Login.vue";

import Gonghao from './views/Gonghao.vue';
import WeixinTool from './views/WeixinTools.vue';
import Statistics from './views/Statistics.vue';
import msgView from './views/msgView.vue';


Vue.use(Router);

export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
        path: "/",
        redirect: '/login'
        }, {
            path: '/login',
            name: '登录',
            component: Login
        }, {
            path: '/home',
            name: '主页',
            redirect: '/gonghao'
        }, {
            path: '/gonghao',
            // name: '公号管理',
            component: Gonghao,
            redirect: '/gonghao/tuoguan',
            children: [
                {
                    path: 'tuoguan',
                    name: '公号托管',
                    component: () => import("@/components/gonghao/tuoguan.vue"),
                    children: [
                        {
                            path: 'create',
                            name: '接管新公号',
                            component: () => import("@/components/gonghao/tuoguan/editInfo.vue"),
                        }, {
                            path: 'update',
                            name: '修改公号',
                            component: () => import("@/components/gonghao/tuoguan/editInfo.vue"),
                        }, {
                            path: 'setting',
                            name: '配置公号',
                            component: () => import("@/components/gonghao/tuoguan/setting.vue"),
                            children: [
                                {
                                    path: 'create',
                                    name: '配置新规则',
                                    component: () => import("@/components/gonghao/tuoguan/editSetting.vue"),
                                }, {
                                    path: 'update',
                                    name: '修改配置',
                                    component: () => import("@/components/gonghao/tuoguan/editSetting.vue"),
                                }
                            ]
                        }
                    ]
                }, {
                    path: 'menu',
                    name: '配置菜单',
                    component: () => import("@/components/gonghao/menuSetting.vue"),
                    children: [
                        {
                            path: 'create',
                            name: '新建菜单',
                            component: () => import("@/components/gonghao/menu/editInfo.vue"),
                        }, {
                            path: 'update',
                            name: '修改菜单',
                            component: () => import("@/components/gonghao/menu/editInfo.vue"),
                        }
                    ]
                }, {
                    path: 'message',
                    name: '回复消息',
                    component: () => import("@/components/gonghao/message.vue"),
                    children: [
                        {
                            path: 'create',
                            name: '创建回复消息',
                            component: () => import("@/components/gonghao/message/editMessage.vue"),
                        }, {
                            path: 'update',
                            name: '编辑回复消息',
                            component: () => import("@/components/gonghao/message/editMessage.vue"),
                        }
                    ]
                }
            ]
        }, {
            path: '/weixinTool',
            // name: '微信工具',
            component: WeixinTool,
            redirect: '/weixinTool/qrCode',
            children: [
                {
                    path: 'qrCode',
                    name: '参数二维码',
                    component: () => import("@/components/weixinTools/qrCode.vue"),
                    children: [
                        {
                            path: 'create',
                            name: '创建二维码',
                            component: () => import("@/components/weixinTools/qrCode/editInfo.vue")
                        }, {
                            path: 'update',
                            name: '编辑二维码',
                            component: () => import("@/components/weixinTools/qrCode/editInfo.vue")
                        }
                    ]
                }, {
                    path: 'tag',
                    name: '标签管理',
                    component: () => import("@/components/weixinTools/tag.vue")
                }
            ]
        }, {
            path: '/statistics',
            // name: '数据统计',
            component: Statistics,
            redirect: '/statistics/fans',
            children: [{
                path: 'fans',
                name: '粉丝数据',
                component: () => import("@/components/statistics/fans.vue")
            }]
        }, {
            path: '/msg_view',
            // name: '客服消息',
            component: msgView,
            redirect: '/msg_view/manage',
            children: [
                {
                    path: 'manage',
                    name: '消息管理',
                    component: () => import("@/components/msgView/manage.vue"),
                    children: [
                        {
                            path: 'create',
                            name: '添加客服消息',
                            component: () => import("@/components/msgView/manage/editInfo.vue")
                        }, {
                            path: 'update',
                            name: '修改客服消息',
                            component: () => import("@/components/msgView/manage/editInfo.vue")
                        }
                    ]
                }
            ]
        }
    ]
});
