<template>
    <div id="app">
        <div class="nav" v-if="$route.path != '/login'">
            <h2 class="logo">后台管理系统</h2>
            <Menu class="menu" mode="horizontal" theme="light" :active-name="selectMenu" @on-select="changeMenu">
                <MenuItem name="公号托管">
                    <Icon type="ios-paper" />
                    公号管理
                </MenuItem>
                <MenuItem name="参数二维码">
                    <Icon type="ios-people" />
                    微信小工具
                </MenuItem>
                <MenuItem name="粉丝数据">
                    <Icon type="ios-stats" />
                    数据统计
                </MenuItem>
                <MenuItem name="消息管理">
                    <Icon type="ios-stats" />
                    客服消息
                </MenuItem>
            </Menu>
        </div>
        <router-view/>
    </div>
</template>

<script>
import store from './store'
export default {
    data() {
        return {
            selectMenu: ''
        }
    },
    methods: {
        changeMenu(name) {
            this.$router.push({name: name})
        }
    },
    beforeMount() {
        let routerName = sessionStorage.getItem("routerName");
        if(this.$route.path == '/' && routerName) {
            this.selectMenu = routerName
        }
        this.$router.beforeEach((to, from, next) => {
            if(from.name == '登录') {
                sessionStorage.setItem("routerName", to.name);
                this.selectMenu = '公号托管'
            }else {
                this.selectMenu = routerName
            }
            next()
        });
    },
    mounted(){
        this.$Message.config({
            top: 100,
            duration: 3
        });
    }
}
</script>


